import fetch from 'node-fetch';
import type {
  CreateGenerationParams,
  CreateGenerationResponse,
  SemanticSearchParams,
  SemanticSearchResult,
} from '@context-labs/models/.dist/services/PortalService';
import { zInstallationChannels } from '@context-labs/models/.dist/enums/InstallationChannels';
import type { BotResolved } from '@context-labs/models/.dist/services/BotService';
import type {
  Query,
  QueryOrigin,
  QueryResolved,
  SetQueryFeedbackParams,
} from '@context-labs/models/.dist/services/QueryService';

const CONTEXT_BASE_URL = 'https://portal.usecontext.io';

export class ContextSDKError extends Error {
  constructor(message: string) {
    super(message);

    // Ensure the name of this error is the same as the class name
    this.name = this.constructor.name;

    // Set stack trace to caller
    if (Error.captureStackTrace) {
      Error.captureStackTrace(this, this.constructor);
    }
  }
}

export type ConfigOpts = {
  apiKey: string;
  baseUrl?: string;
  origin?: QueryOrigin;
};

export type {
  Query,
  QueryResolved,
  SemanticSearchResult,
  SemanticSearchParams,
  SetQueryFeedbackParams,
};

export type QueryParams = {
  botId: string;
  query: string;
  onData?: (chunk: string) => void;
  onComplete?: (query: QueryResolved) => void;
  onError?: (error: ContextSDKError) => void;
};

export type QueryResponse = {
  controller?: AbortController;
};

/**
 * ContextSDK class
 */
export class ContextSDK {
  apiKey: string;

  baseUrl: string;

  origin: QueryOrigin;

  constructor(config: ConfigOpts) {
    if (!config.apiKey) {
      // throw new ContextSDKError('API Key is required. Sign up at http://usecontext.com');
      console.error(
        'An API Key will be required soon. Sign up at http://usecontext.com.'
      );
    }

    this.apiKey = config.apiKey ?? '';
    this.baseUrl = config.baseUrl || CONTEXT_BASE_URL;
    this.origin = config.origin || {
      channel: zInstallationChannels.Enum.API,
      originId: '',
      originName: '',
    };
  }

  #url(path: string): string {
    return `${this.baseUrl}${path}`;
  }

  async search({
    botId,
    query,
    topK = 20,
  }: {
    botId: string;
    query: string;
    topK?: number;
  }): Promise<SemanticSearchResult> {
    const fetchBotResponse = await fetch(this.#url('/api/bot'), {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'X-API-Key': this.apiKey,
      },
      body: JSON.stringify({ botId }),
    });
    if (!fetchBotResponse.ok) {
      throw new ContextSDKError('Error fetching bot.');
    }

    const bot = (await fetchBotResponse.json()) as BotResolved;

    const params: SemanticSearchParams = {
      botId,
      sourceIds: bot.sourceIds,
      query,
      distinctId: '',
      topK,
      origin: this.origin,
    };

    const response = await fetch(this.#url('/api/search-snippets'), {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'X-API-Key': this.apiKey,
      },
      body: JSON.stringify(params),
    });

    if (!response.ok) {
      throw new ContextSDKError(response.statusText);
    }

    const { snippets } = (await response.json()) as SemanticSearchResult;

    return {
      snippets,
    };
  }

  async query({
    botId,
    query,
    onData,
    onComplete,
    onError,
  }: QueryParams): Promise<QueryResponse> {
    const controller = new AbortController();

    const doQuery = async () => {
      const fetchBotResponse = await fetch(this.#url('/api/bot'), {
        signal: controller.signal,
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-API-Key': this.apiKey,
        },
        body: JSON.stringify({ botId }),
      });
      if (!fetchBotResponse.ok) {
        onError?.(new ContextSDKError(fetchBotResponse.statusText));
        return null;
      }

      const bot = (await fetchBotResponse.json()) as BotResolved;

      const createGenerationParams: CreateGenerationParams = {
        botId,
        sourceIds: bot.sourceIds,
        query,
        distinctId: '',
        origin: this.origin,
      };

      const response = await fetch(this.#url('/api/create-generation'), {
        signal: controller.signal,
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'X-API-Key': this.apiKey,
        },
        body: JSON.stringify(createGenerationParams),
      });

      if (!response.ok) {
        onError?.(new ContextSDKError(response.statusText));
        return null;
      }

      if (!response.body) {
        onError?.(new ContextSDKError('No data returned from query.'));
        return null;
      }

      const stream = response.body;
      let completion = '';
      const delimiter = '___DELIMITER___';

      /**
       * For some reason the last character is somtimes missing
       * I have no idea why. It may have something to do with
       * event sourcing and the way the data is streamed.
       * TODO (sam): Look into issues solved by the eventsource-parser library
       *
       * What we do for now is try to try to parse as soon
       * as we see the delmliter and on every chunk after that.
       * Eventually the stream should complete and we should have a
       * valid JSON object to parse.
       */
      stream.on('data', async (chunk) => {
        const chunkString = chunk.toString();
        completion += chunkString;

        if (completion.includes(delimiter)) {
          let result: CreateGenerationResponse | null = null;
          try {
            const [_, json] = completion.split(delimiter);
            result = JSON.parse(json);
          } catch (e) {
            // Supress error
            // console.error(e);
          }

          if (result?.query) {
            onComplete?.({
              ...result.query,
              snippets: result?.snippets ?? [],
            });
          }
        } else {
          onData?.(chunkString);
        }
      });
    };

    doQuery();
    return { controller };
  }

  setQueryFeedback = async (params: SetQueryFeedbackParams): Promise<Query> => {
    const response = await fetch(this.#url('/api/query-feedback'), {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(params),
    });

    const query = (await response.json()) as Query;
    return query;
  };
}
